<template>


    <div class="style-editing-panel" v-if="activeComponentID">
    <el-tabs stretch>
      <el-tab-pane label="样式" name="0">
        <div class="menu-wrapper">
          <background-change :background="background" @change="backgroundChange" />
          <slider
            :value="opacity"
            :opts="{placeholder: '透明度',inputBox: true,hasRate: true}"
            @valueChange="opacityChange"
          />
          <a-divider />
                      <div class="title-wrapper" style="margin-bottom: 10px;">
              <span>线条尺寸</span>
            </div>
            <slider
            :value="lineHeight"
            :opts="{placeholder: '高度',inputBox: true,hasPx: true}"
            @valueChange="lineHeightChange"
          />
                      <slider
            :value="lineWidth"
            :opts="{placeholder: '宽度',inputBox: true,hasPx: true}"
            @valueChange="lineWidthChange"
          />
                    <a-divider />
                                          <div class="title-wrapper" style="margin-bottom: 10px;">
              <span>线条样式</span>            </div>
  <a-radio-group name="line-style-group" :defaultValue="borderStyle" class="line-style-group" @change="borderStyleChange">
    <a-radio :value="'solid'">实线</a-radio>
    <a-radio :value="'dashed'">方形虚线</a-radio>
    <a-radio :value="'dotted'">圆形虚线</a-radio>
  </a-radio-group>
                                        <a-divider />

          <margin-edit @valueChange="marginChange" />
        </div>
      </el-tab-pane>
      <el-tab-pane label="动画" name="1"></el-tab-pane>
    </el-tabs>
  </div>

</template>

<script>
import styleInput from "@/components/StyleMenuComponents/styleInput.vue";
import slider from "@/components/StyleMenuComponents/slider.vue";
import bgChange from "@/components/StyleMenuComponents/backgroundChange.vue";
import marginEdit from "@/components/StyleMenuComponents/marginEdit.vue";
import mixinObject from "@/mixin.js";
const {
  componentStoreBaseOperate,
  backgroundUpdateOperate,
  opacityUpdateOperate,
  marginUpdateOperate
} = mixinObject;
export default {
    mixins: [
    componentStoreBaseOperate,
    backgroundUpdateOperate,
    opacityUpdateOperate,
    marginUpdateOperate
  ],
  components: {
    "style-input": styleInput,
    "margin-edit": marginEdit,
    "background-change": bgChange,
    slider: slider
  },
  data(){
    return {
    }
  },
  methods: {
    borderStyleChange(e){
      let payload = {
        id: this.activeComponentID,
        value: {
          componentStyle: {
            "border-style": e.target.value
          }
        }
      };
      this.updateComponentInfo(payload);
    },
    lineHeightChange(value) {
      let payload = {
        id: this.activeComponentID,
        value: {
          componentStyle: {
            "border-top-width": `${value}px`
          }
        }
      };
      this.updateComponentInfo(payload);
    },
    lineWidthChange(value) {
      let payload = {
        id: this.activeComponentID,
        value: {
          componentStyle: {
            width: `${value}%`
          }
        }
      };
      this.updateComponentInfo(payload);
    },
    colorChange(hexStr) {
      let payload = {
        id: this.activeComponentID,
        value: {
          componentStyle: {
            "background-color": hexStr
          }
        }
      };
      this.updateComponentInfo(payload);
    }
  },
  computed: {
    borderStyle(){
      return this.activatedComponent.componentInfo.componentStyle["border-style"]
    },
    color() {
      return this.activatedComponent.componentInfo.componentStyle["border-color"];
    },
    lineHeight(){
      return this.activatedComponent.componentInfo.componentStyle["border-top-width"]
    },
    lineWidth(){
      return this.activatedComponent.componentInfo.componentStyle.width
    }
  },
  mounted(){
  }
};
</script>
<style lang="stylus" scoped>
.menu-wrapper
  .line-style-group
    .ant-radio-wrapper
      margin-right 0
  .button-edit
    .input-ground
      display grid
      grid-template-columns 1fr 1fr
      .input
        margin-top 12px
        display flex
        align-items center
        div
          margin-left 8px
          height 22px
          width 50px
          position relative
</style>
